@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300&family=Poppins&display=swap');
.home {
  text-align: center;
  height: 100vh;
  padding: 25px;
}

.logo {
  width: 250px;
  height: auto;
  margin: 40px 0;
}

.home h1 {
  font-family: 'Oswald', sans-serif;
  color: black;
  font-size: 3rem;
  text-transform: uppercase;
  font-weight: bold;
  text-shadow: 4px 3px 0px #fff, 2px 2px 0px rgba(0, 0, 0, 0.15);
}

.signup-button {
  margin-top: 50px;
  background-color: #000 !important;
  border: none !important ;
}

.login-button {
  margin-top: 50px;
  background-color: #2eafa5 !important;
  border: none !important ;
}

.buttons {
  padding: 5%;
  font-family: 'Poppins', sans-serif;
}
